@use "../../foundation/mixins" as *;
@use "../../foundation/variables" as *;
// MV部分
.p-blog-mv {
  position: relative;
  // 背景画像に影を付ける
  &__wrapper {
    &::after {
      background-color: rgba(80, 80, 80, 0.502);
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
    }
  }
  // タイトルの配置
  &__title {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: grid;
    place-content: center;
  }
  // タイトル文字部分
  &__titleInner {
    text-align: center;
    z-index: 99;
    color: $color-white;
    font-size: vw(25);
    line-height: calc(60 / 25);
    font-weight: bold;
    @include sp {
      width: 30rem;
      font-size: 3rem;
      line-height: calc(50 / 30);
    }
  }
}

.p-blog-list {
  padding: 6.5rem 0 20rem 0;
  &__items {
    margin-top: 4rem;
  }
  &__item {
    display: flex;
    // display: grid;
    // grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
  }
  &__image {
    width: 100%;
  }
  // &__text-inner {
  // }
  &__subTitle {
    h3 {
      font-size: 2rem;
      line-height: calc(25 / 20);
      font-weight: bold;
    }
  }
  &__date {
    margin: 0.3rem 0 1.1rem 0;
    time {
      line-height: calc(30 / 17);
      font-size: 1.7rem;
    }
  }
  &__description {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 57rem;
    p {
      font-size: 1.8rem;
      line-height: calc(30 / 18);
    }
  }
}
